
.weui-tab .weui-tab__panel{padding-bottom:13.33333vw
}
.weui-tab .weui-tabbar{background-color:#fff
}
.weui-tab .weui-tabbar:before{border-top-color:rgba(0,0,0,.2)
}
.weui-tab .weui-tabbar .weui-tabbar__icon{height:auto;width:auto
}
.weui-tab .weui-tabbar .weui-tabbar__icon .icon{margin:1.06667vw 0 .8vw;font-size:5.33333vw
}
.weui-tab .weui-tabbar .weui-tabbar__icon .icon-round{width:10.66667vw;height:10.66667vw;padding:2.66667vw;box-sizing:border-box;background:-webkit-linear-gradient(315deg,#006cff,#2a30ea);background:linear-gradient(135deg,#006cff,#2a30ea);border-radius:50%
}
.weui-tab .weui-tabbar .weui-tabbar__icon .icon-round .icon{margin:0;color:#fff
}
.weui-tab .weui-tabbar .weui-tabbar__label{font-size:2.66667vw;line-height:3.73333vw;margin-bottom:1.33333vw
}


