
html{-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%
}
body{line-height:1.6;font-family:-apple-system-font,Helvetica Neue,sans-serif
}
*{margin:0;padding:0
}
a img{border:0
}
a{text-decoration:none;-webkit-tap-highlight-color:rgba(0,0,0,0)
}
::-webkit-input-placeholder{font-family:-apple-system-font,Helvetica Neue,sans-serif
}
a{-webkit-touch-callout:none
}
.vux-1px,.vux-1px-b,.vux-1px-l,.vux-1px-r,.vux-1px-t,.vux-1px-tb{position:relative
}
.vux-1px:before{content:" ";position:absolute;left:0;top:0;width:200%;border:1px solid #c7c7c7;color:#c7c7c7;height:200%;-webkit-transform-origin:left top;transform-origin:left top;-webkit-transform:scale(.5);transform:scale(.5)
}
.vux-1px-t:before{top:0;border-top:1px solid #c7c7c7;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleY(.5);transform:scaleY(.5)
}
.vux-1px-b:after,.vux-1px-t:before{content:" ";position:absolute;left:0;right:0;height:1px;color:#c7c7c7
}
.vux-1px-b:after{bottom:0;border-bottom:1px solid #c7c7c7;-webkit-transform-origin:0 100%;transform-origin:0 100%;-webkit-transform:scaleY(.5);transform:scaleY(.5)
}
.vux-1px-tb:before{top:0;border-top:1px solid #c7c7c7;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleY(.5);transform:scaleY(.5)
}
.vux-1px-tb:after,.vux-1px-tb:before{content:" ";position:absolute;left:0;right:0;height:1px;color:#c7c7c7
}
.vux-1px-tb:after{bottom:0;border-bottom:1px solid #c7c7c7;-webkit-transform-origin:0 100%;transform-origin:0 100%;-webkit-transform:scaleY(.5);transform:scaleY(.5)
}
.vux-1px-l:before{left:0;border-left:1px solid #c7c7c7;-webkit-transform-origin:0 0;transform-origin:0 0;-webkit-transform:scaleX(.5);transform:scaleX(.5)
}
.vux-1px-l:before,.vux-1px-r:after{content:" ";position:absolute;top:0;width:1px;bottom:0;color:#c7c7c7
}
.vux-1px-r:after{right:0;border-right:1px solid #c7c7c7;-webkit-transform-origin:100% 0;transform-origin:100% 0;-webkit-transform:scaleX(.5);transform:scaleX(.5)
}
.icon{width:1em;height:1em;vertical-align:-.15em;fill:currentColor;overflow:hidden
}
.weui-tab .vux-header{position:absolute;top:0;left:0;width:100%;padding:5.86667vw 0 .53333vw;background:-webkit-linear-gradient(left,#2a30ea,#006cff);background:linear-gradient(90deg,#2a30ea,#006cff);z-index:1
}
.weui-tab .vux-header .vux-header-left,.weui-tab .vux-header .vux-header-right{top:8.8vw
}
.weui-tab .vux-header .vux-header-left{left:4vw
}
.weui-tab .vux-header .vux-header-left .left-arrow:before{border-color:#fff
}
.weui-tab .vux-header .vux-x-icon-navicon{position:relative;top:-1.06667vw;left:-.8vw;fill:#fff;width:6.93333vw;height:6.93333vw
}
.weui-tab .vux-header .vux-header-title{font-size:4.8vw
}
.weui-tab .vux-header .vux-header-right{color:#fff
}
.weui-tab .weui-tab__panel{padding-top:17.06667vw
}
.vux-tab-wrap{box-shadow:0 1px 1.33333vw 0 rgba(0,0,0,.2)
}
.vux-tab-wrap .vux-tab-container .vux-tab .vux-tab-item{font-size:4vw
}
.vux-badge.plain{border:1px solid #f66;box-sizing:border-box;line-height:4vw;background:transparent;color:#f66
}
.list-card{margin-top:4vw;overflow:hidden;padding-bottom:1.33333vw
}
.list-card .weui-panel{margin-top:4vw;box-shadow:0 1px 1.33333vw 0 rgba(0,0,0,.2)
}
.vux-cell-value{font-size:3.46667vw!important;font-family:PingFangSC-Regular!important;font-weight:400!important;color:#333!important;padding-right:6.13333vw
}
.weui-icon-warn:before{content:"\e7db"
}
.weui-cell_access .weui-cell__ft:after{display:none!important
}
.weui-cell_access .weui-cell__ft{padding-right:0!important
}
.weui-cells{height:13.6vw!important
}
body,html{width:100%;overflow-x:hidden
}
#app,body,html{height:100%
}


