
.csy{position:relative;width:14.93333vw;height:14.93333vw;background:-webkit-linear-gradient(350deg,#006cff,#fff 90%,#fff);background:linear-gradient(100deg,#006cff,#fff 90%,#fff)
}
.csy,.csy:before{border-radius:50%
}
.csy:before{content:"";text-align:center;display:block;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%,-50%);transform:translate(-50%,-50%);width:9.6vw;height:9.6vw;line-height:9.6vw;background:#fff
}
.home{background:#f6f6f6
}
.home .vux-tab-item{background:none!important
}
.home .b-card{margin-bottom:4vw
}
.home .week-header{padding:4.26667vw 4vw 4vw 4.26667vw
}
.home-header-top{text-align:center;height:35.73333vw;border-radius:0 0 13.33333vw 13.33333vw;background:-webkit-linear-gradient(left,#2a30ea,#006cff);background:linear-gradient(90deg,#2a30ea,#006cff)
}
.home-header-top .vux-swiper{border-radius:2.66667vw;box-shadow:.53333vw 1.33333vw 2.4vw #444;height:48vw!important
}
.home-header-top .swiper-demo{padding:1.33333vw 4vw 4vw
}
.home-header-top .swiper-demo .swiper-demo-img{position:relative;height:48vw
}
.home-header-top .swiper-demo .swiper-demo-img img{width:100%;height:100%;background-size:cover;border-radius:2.66667vw
}
.home-header-top .swiper-demo .swiper-demo-img span{position:absolute;padding:1.06667vw 1.33333vw 1.06667vw 1.86667vw;bottom:3.73333vw;right:4vw;color:#fff;background-color:rgba(0,0,0,.7);font-size:3.46667vw;border-radius:1.06667vw;z-index:1
}
.home-header-middle{margin-top:14.66667vw;padding:4vw 4vw 5.33333vw;border-bottom:1px solid rgba(0,0,0,.2)
}
.home-header-middle p:first-child{font-size:5.6vw;font-weight:500;color:#2a30ea;padding-bottom:1.33333vw
}
.home-header-middle p:last-child{font-size:4vw;font-weight:500
}
.home-header-middle p:last-child span{color:#2a30ea
}
.home-header-bottom{padding-top:1.33333vw
}
.home-header-bottom .vux-tab-item{font-size:4vw!important
}
.home-header-bottom .swiper-bottom{padding:0 4vw
}
.home-header-bottom .swiper-bottom .main .tab-swiper{padding-left:2.66667vw
}
.home-header-bottom .swiper-bottom .main .tab-swiper .icon{padding-right:3.2vw
}
.home-header-bottom .tab-swiper{border-bottom:1px dotted #ddd;height:11.73333vw;line-height:11.73333vw
}
.home-header-bottom .vux-swiper{height:35.46667vw!important;overflow-y:auto!important
}


